body, html {
  height: 100%;
  margin: 0;
  font-family: 'Roboto', Arial, sans-serif;
  background: #f7f9fa;
}

.container {
  max-width: 400px;
  margin: 2rem auto;
  background: #fff;
  border-radius: 12px;
  box-shadow: 0 2px 8px rgba(0,0,0,0.07);
  padding: 2rem;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.logo {
  width: 120px;
  margin-bottom: 1rem;
}

.form {
  width: 100%;
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.form input {
  padding: 0.75rem;
  border: 1px solid #d0d7de;
  border-radius: 6px;
  font-size: 1rem;
}

.cta {
  background: #1976d2;
  color: #fff;
  border: none;
  padding: 0.75rem;
  border-radius: 6px;
  font-size: 1.1rem;
  cursor: pointer;
  font-weight: 500;
  margin-top: 0.5rem;
  transition: background 0.2s;
}

.cta:hover {
  background: #1565c0;
}

.info {
  font-size: 0.95rem;
  color: #555;
  margin-top: 0.5rem;
}

.voice-agent {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 1.5rem;
}

.status {
  font-size: 1.1rem;
  color: #1976d2;
  margin-bottom: 1rem;
}

.transcript {
  width: 100%;
}

.transcript-box {
  min-height: 60px;
  max-height: 200px;
  overflow-y: auto;
  background: #f0f4f8;
  border-radius: 6px;
  padding: 0.75rem;
  font-size: 0.95rem;
  color: #333;
  white-space: pre-wrap;
  word-wrap: break-word;
  font-family: 'Courier New', monospace;
}

.calendar-log {
  width: 100%;
  margin-top: 1rem;
}

.calendar-log h3 {
  margin: 0.5rem 0;
  font-size: 0.95rem;
  color: #666;
}

.log-box {
  min-height: 40px;
  max-height: 150px;
  overflow-y: auto;
  background: #fff3e0;
  border-left: 3px solid #ff9800;
  border-radius: 4px;
  padding: 0.5rem;
  font-size: 0.85rem;
  color: #333;
  white-space: pre-wrap;
  word-wrap: break-word;
  font-family: 'Courier New', monospace;
  line-height: 1.4;
}

.end-call {
  background: #e53935;
  color: #fff;
  border: none;
  padding: 0.75rem 1.5rem;
  border-radius: 6px;
  font-size: 1rem;
  cursor: pointer;
  font-weight: 500;
  margin-top: 1rem;
  transition: background 0.2s;
}

.end-call:hover {
  background: #b71c1c;
}

.success {
  text-align: center;
  color: #1976d2;
}

.success h2 {
  margin-bottom: 1.5rem;
}

.success p {
  margin: 0.75rem 0;
  font-size: 0.95rem;
}

@media (max-width: 600px) {
  .container {
    max-width: 98vw;
    padding: 1rem;
  }
  .form input, .cta, .end-call {
    font-size: 1rem;
    padding: 0.65rem;
  }
  .transcript-box {
    max-height: 150px;
  }
  .log-box {
    max-height: 120px;
  }
}
